	export default {
		data() {
			return {
			background: ['red', 'blue', 'pink'],
            indicatorDots: true,
			indicator1:false,
            autoplay: true,
            interval: 2000,//间隔
            duration: 500,//
			bannerList:[],
			}
		},
		onLoad:function(){ 
			var _self = this
			_self.getBanner()
		},
		methods: {
			getBanner:function(){
				var _self = this
				uni.request({
					url:_self.$api+"ybyfManager/dockingManager/pictureQuery",
					success:function(res){
						console.log(res)
						_self.bannerList = res.data
					}
				})
			},
			gotoPostMessage:function(){
				uni.navigateTo({
					url:"../postMessage/postMessage"
				})
			}
		}
	}
